import { all, takeEvery } from "redux-saga/effects";

import { sagaUsers, sagaLogin, sagaAddUser, sagaEditUser, sagaDeleteUser  } from "./userSaga";
import { sagaEquipes, sagaAddEquipe, sagaEditEquipe, sagaDeleteEquipe  } from "./equipeSaga";
import { sagaPerfis } from "./perfilSaga";
import { sagaCategorias, sagaAddCategorias, sagaEditCategoria, sagaDeleteCategoria } from "./categoriaSaga";


export default function* rootSagas() {
    yield all([
        takeEvery('SAGA_SET_USERS', sagaUsers),
        takeEvery('SAGA_SET_LOGIN', sagaLogin),
        takeEvery('SAGA_ADD_USER', sagaAddUser),
        takeEvery('SAGA_EDIT_USER', sagaEditUser),
        takeEvery('SAGA_DELETE_USER', sagaDeleteUser),

        takeEvery('SAGA_SET_PERFIS', sagaPerfis),
        
        takeEvery('SAGA_SET_CATEGORIAS', sagaCategorias),
        takeEvery('SAGA_ADD_CATEGORIAS', sagaAddCategorias),
        takeEvery('SAGA_EDIT_CATEGORIA', sagaEditCategoria),
        takeEvery('SAGA_DELETE_CATEGORIA', sagaDeleteCategoria),

        takeEvery('SAGA_SET_EQUIPES', sagaEquipes),
        takeEvery('SAGA_ADD_EQUIPE', sagaAddEquipe),
        takeEvery('SAGA_EDIT_EQUIPE', sagaEditEquipe),
        takeEvery('SAGA_DELETE_EQUIPE', sagaDeleteEquipe),
    ])
}